            <p><button @click="closeCard">close</button><br /><br /></p>
          </div>
          <div v-show="isShownThree" class="card">
            <Card :quote="sendQuote" />
            <p>
              <img
                src="../assets/whitearrow.png"
                style="width: 2%;"
                @click="newQuote"
              /><br /><br />
              <button @click="sendCard">close</button><br /><br />
            </p>
          </div>
          <p v-show="isShownOne" @click="openOverlay" class="animate">
            <img src="../assets/icon.png" />
