<script>
import axios from "axios";
import Instructions from "../components/Instructions";
import Card from "../components/Card";

export default {
  name: "home",
  components: {
    Instructions,
    Card,
  },
  data() {
    return {
      isShown: true,
      isShownOne: false,
      isShownTwo: false,
      isShownThree: false,
      quote: "",
      anotherQuote: "",
      sendQuote: "",
    };
  },
  methods: {
    closeInstructions: function() {
      console.log("instructions closed");
      this.isShown = false;
      this.isShownOne = true;
    },
    openOverlay: function() {
      console.log("open overlay");
      this.isShownTwo = true;
      this.isShownOne = false;
    },
    closeCard: function() {
      this.isShownTwo = false;
      this.isShown = true;
      location.reload();
      // can take this out?
    },
    makeNew: function() {
      this.isShown = false;
      this.isShownThree = true;
    },
    sendCard: function() {
      this.isShownThree = false;
      this.isShown = true;
      location.reload();
    },
    newQuote: function() {
      axios
        .get("https://quote-garden.herokuapp.com/quotes/random")
        .then((res) => (this.anotherQuote = res.data.quoteText))
        .catch((err) => console.log(err));
      this.sendQuote = this.anotherQuote;
    },
  },
  mounted() {},
  created() {
    axios
      .get("https://quote-garden.herokuapp.com/quotes/random")
      .then((res) => (this.quote = res.data.quoteText))
      .catch((err) => console.log(err));
    axios
      .get("https://quote-garden.herokuapp.com/quotes/random")
      .then((res) => (this.sendQuote = res.data.quoteText))
      .catch((err) => console.log(err));
  },
};
</script>

